import { React, Fragment, useEffect, useState } from "react";
import Header from "../Header/Header";
import { NavLink } from "react-router-dom";
import NumberFormat from "react-number-format";
import { useSelector, useDispatch } from "react-redux";
import { cart_slice_action } from "../../redux/cart_slice.js";
import { useForm } from "react-hook-form";
import { auth_action } from "../../redux/auth_slice.js";
import { useNavigate } from "react-router-dom";
import OrderService from "../../api/OrderService";
import { toast } from "react-toastify";
import Modal from "react-bootstrap/Modal";
function Checkout(props) {
  //var
  const dispatch = useDispatch();
  const isLoggedIn = useSelector((state) => state.auth_slice.isLoggedIn);
  const user = useSelector((state) => state.auth_slice.user);

  const items = useSelector((state) => state.cart_slice.items);
  const totalItem = useSelector((state) => state.cart_slice.totalItem);
  const totalPrice = useSelector((state) => state.cart_slice.totalPrice);

  const navigate = useNavigate();

  var District = [
    "Qun 1",
    "Qun 2",
    "Qun 3",
    "Qun 4",
    "Qun 5",
    "Qun 6",
    "Qun 7",
    "Qun 8",
    "Qun 9",
    "Qun 10",
    "Qun 11",
    "Qun 12",
    "Qun Th c",
    "Qun G Vp",
    "Qun Bnh Thnh",
    "Qun Tn Bnh",
    "Qun Tn Ph",
    "Qun Ph Nhun",
    "Qun Bnh Tn",
    "Huyn C Chi",
    "Huyn Hc Mn",
    "Huyn Bnh Chnh",
    "Huyn Nh B",
    "Huyn Cn Gi",
  ];

  var Ward = [
    "Phng 1",
    "Phng 2",
    "Phng 3",
    "Phng 4",
    "Phng 5",
    "Phng 6",
    "Phng 7",
    "Phng 8",
    "Phng 9",
    "Phng 10",
    "Phng 11",
    "Phng 12",
    "Phng 13",
    "Phng 14",
    "Phng 15",
  ];

  let {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const [showLoadingModal, setShowLoadingModal] = useState(false);

  const handleCloseLoadingModal = () => setShowLoadingModal(false);
  const handleShowLoadingModal = () => setShowLoadingModal(true);
  //function

  window.scrollTo(0, 0);
  function getAddressString(addno, street, ward, district, city) {
    return `${addno} ${street}, ${ward}, ${district}, ${city}`;
  }

  function SendOrder(data) {
    setShowLoadingModal(true);
    var date = new Date();
    var order = {
      userID: user.id,
      contactName: data.contactName,
      address: getAddressString(
        data.addressNo,
        data.street,
        data.ward,
        data.district,
        data.city
      ),
      phone: data.phoneNumber,
      email: data.email,
      paymentMethod: data.paymentMethod,
      orderDate: date.toISOString(),
      totalItem: totalItem,
      totalPrice: totalPrice,
      status: 0,
      note: data.note,
      orderDetails: [],
      discountCodeID: null,
    };
    //console.log(order.orderDate)

    items.forEach((item) => {
      var orderItem = {
        bookId: item.product.id,
        quantity: item.quantity,
        promotionPercent: item.product.promotionInfo
          ? item.product.promotionInfo.promotionPercent
          : null,
        promotionAmount: item.product.promotionInfo
          ? item.product.promotionInfo.promotionAmount
          : null,
      };
      order.orderDetails.push(orderItem);
    });
    // console.log(order);
    localStorage.setItem("order",JSON.stringify(order))
    if (order.paymentMethod == "vnpay") {
      OrderService.GetVnPayUrl(order.totalPrice)
        .then((response) => {
          var url = response.data.paymentUrl
          window.location.href=url
        })
        .catch((error) => {
          console.log(error);
        })
        .finally(() => {});
    }
    else{
      OrderService.PostOrder(order)
      .then((response) => {
        if (response.data.success) {
          dispatch(cart_slice_action.resetCart());
          toast.success("t n hng thnh cng!", {
            position: "top-center",
            autoClose: 1000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
          });
          navigate("/thankyou");
        } else {
          alert("C li xy ra!");
        }
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {
        setShowLoadingModal(false);
        localStorage.removeItem("order")
      });
    }
   
  }
  useEffect(() => {
    dispatch(auth_action.getAuthInfoFromLocalStorage());
  }, [isLoggedIn, dispatch]);

  return (
    <Fragment>
      <Header></Header>
      <div className="container-fluid">
        <nav aria-label="breadcrumb" className="mt-2 breadcrumb_nav">
          <ol className="breadcrumb mt-2 ms-2">
            <li className="breadcrumb-item">
              <NavLink to="/">Home</NavLink>
            </li>
            <li className="breadcrumb-item active" aria-current="page">
              <p>Checkout</p>
            </li>
          </ol>
        </nav>
      </div>
      <div className="container">
        <div className="row ">
          <div className="col-3 d-flex justify-content-center border-bottom border-end border-4">
            <img
              src="https://res.cloudinary.com/dkmk9tdwx/image/upload/v1628192627/logo_v5ukvv.png"
              style={{ height: "65px", width: "65px" }}
              alt="Nav Icon"
              className="d-inline"
            />
            <p className="lead mt-3 fw-bold">Circle's Shop</p>
          </div>
          <div className="col-9 border-bottom border-4">
            <p className="lead mt-3">
              <i className="fas fa-money-check-alt me-2"></i>Thanh ton
            </p>
          </div>
        </div>
      </div>

      <div className="container mt-4 ">
        <div className="row">
          {isLoggedIn && totalItem == 0 && (
            <div className="col-sm-12 col-lg-9 p-2 border border-2 animate__animated animate__bounceInLeft">
              <img
                className="img-fluid"
                src="https://www.gamkart.com/frontend/img/empty-cart.png"
                alt="noproduct"
              ></img>
            </div>
          )}
          {!isLoggedIn && (
            <div className="col-sm-12 col-lg-9 p-2 border border-2 animate__animated animate__bounceInLeft">
              <div className="container d-flex flex-column">
                <p className="text-center fw-bold">Hy ng nhp  tip tc</p>
                <button
                  className="btn btn-success"
                  onClick={() => {
                    localStorage.setItem("redirect", "/checkout");
                    navigate("/login");
                  }}
                >
                  <i className="fas fa-sign-in-alt me-2"></i>ng nhp
                </button>
              </div>
            </div>
          )}
          {isLoggedIn && totalItem > 0 && (
            <div className="col-sm-12 col-lg-9 p-2 border border-2 animate__animated animate__bounceInLeft">
              <div className="container ">
                <form>
                  <p className="fw-bold lead">Thng tin lin lc</p>
                  <div className="mb-3">
                    <label className="form-label">Tn lin lc: </label>
                    <input
                      type="text"
                      className="form-control"
                      {...register("contactName", {
                        required: true,
                      })}
                    ></input>
                    {errors.contactName?.type === "required" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle me-2"></i>
                        Tn lin lc cha in
                      </div>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Email: </label>
                    <input
                      type="email"
                      className="form-control"
                      {...register("email", {
                        required: true,
                        pattern:
                          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                      })}
                    ></input>
                    {errors.email?.type === "required" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle me-2"></i>
                        Email cha in
                      </div>
                    )}
                    {errors.email?.type === "pattern" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle me-2"></i>
                        Email khng hp l
                      </div>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label">S in thoi: </label>
                    <input
                      type="number"
                      className="form-control"
                      {...register("phoneNumber", {
                        required: true,
                        minLength: 9,
                        maxLength: 10,
                      })}
                    ></input>
                    {errors.phoneNumber?.type === "required" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle"></i>SDT khng
                         trng
                      </div>
                    )}
                    {errors.phoneNumber?.type === "minLength" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle"></i>SDT khng
                        hp l
                      </div>
                    )}
                    {errors.phoneNumber?.type === "maxLength" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle"></i>SDT khng
                        hp l
                      </div>
                    )}
                  </div>
                  <p className="fw-bold lead">a ch nhn hng</p>
                  <div className="mb-3">
                    <label className="form-label">S nh: </label>
                    <input
                      type="text"
                      className="form-control"
                      {...register("addressNo", {
                        required: true,
                      })}
                    ></input>
                    {errors.addressNo?.type === "required" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle me-2"></i>S
                        nh cha in
                      </div>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label">ng: </label>
                    <input
                      type="text"
                      className="form-control"
                      {...register("street", {
                        required: true,
                      })}
                    ></input>
                    {errors.street?.type === "required" && (
                      <div className="form-text">
                        <i className="fas fa-exclamation-triangle me-2"></i>
                        Tn ng cha in
                      </div>
                    )}
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Phng: </label>
                    <select
                      className="form-select"
                      defaultValue={"Phng 1"}
                      {...register("ward", {
                        required: true,
                      })}
                    >
                      {Ward.map((item) => {
                        return (
                          <option value={item} key={item}>
                            {item}
                          </option>
                        );
                      })}
                    </select>
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Qun: </label>
                    <select
                      className="form-select"
                      defaultValue={"Qun 1"}
                      {...register("district", {
                        required: true,
                      })}
                    >
                      {District.map((item) => {
                        return (
                          <option value={item} key={item}>
                            {item}
                          </option>
                        );
                      })}
                    </select>
                  </div>
                  <div className="mb-3">
                    <label className="form-label">Thnh ph: </label>
                    <select
                      className="form-select"
                      defaultValue={"TP.HCM"}
                      {...register("city", {
                        required: true,
                      })}
                    >
                      <option value="TP.HCM">TP.HCM</option>
                    </select>
                  </div>
                  <p className="fw-bold lead">Ghi ch </p>
                  <div className="mb-3">
                    <label className="form-label">Ghi ch: </label>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Ghi ch cho n hng"
                      {...register("note")}
                    ></input>
                    {/* <div  className="form-text">
                We'll never share your email with anyone else.
              </div> */}
                  </div>
                  <p className="fw-bold lead">Phng thc thanh ton</p>
                  <div className="mb-3">
                    <label className="form-label">Phng thanh ton: </label>
                    <select
                      className="form-select"
                      defaultValue={"cash"}
                      {...register("paymentMethod", {
                        required: true,
                      })}
                    >
                      <option value="cash">Tin mt</option>
                      <option value="vnpay">VNPay</option>
                    </select>
                  </div>
                </form>
              </div>
            </div>
          )}

          <div className="col-sm-12 col-lg-3  p-2 border border-2 animate__animated animate__bounceInRight">
            <div className="checkout_sticky">
              <p className="lead text-center fw-bold">Thng tin gi hng</p>
              <div className="container d-flex justify-content-center flex-column text-center text-monospace mb-3">
                <p>
                  Tng gi tr :{" "}
                  <NumberFormat
                    value={totalPrice}
                    className="text-center   "
                    displayType={"text"}
                    thousandSeparator={true}
                    suffix={""}
                    renderText={(value, props) => (
                      <span {...props}>{value}</span>
                    )}
                  />
                </p>
                <p>Tng s sn phm : {totalItem}</p>
                <div className="d-grid gap-2">
                  <button
                    className="btn btn-warning"
                    type="button"
                    onClick={handleSubmit(SendOrder)}
                    disabled={items.length == 0 || !isLoggedIn ? true : false}
                  >
                    <i className="fas fa-gift me-2"></i> Gi n hng
                  </button>
                </div>
              </div>
              <p className="lead text-center fw-bold">Thng tin m gim gi</p>
              <div className="container">
                <p className="text-center">Khng c thng tin m gim gi</p>
                <div className="d-grid gap-2">
                  <button
                    className="btn btn-warning"
                    type="button"
                    onClick={handleShowLoadingModal}
                  >
                    <i className="fas fa-gift me-2"></i> Nhp m gim gi
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        backdrop="static"
        centered
        show={showLoadingModal}
        onHide={handleCloseLoadingModal}
      >
        <Modal.Header closeButton>
          <Modal.Title>ang gi n hng...</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="d-flex flex-column align-items-center">
            <div className="spinner-border text-info" role="status">
              <span className="visually-hidden">Loading...</span>
            </div>

            <p className="text-center text-monospace mt-2">
              ang x l xin ch 1 xu ...
            </p>
          </div>
        </Modal.Body>
      </Modal>
    </Fragment>
  );
}

export default Checkout;
